import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			type:'store'
		}
	},
	onLoad(options) {
		this.type = options.type;
	},
	methods: {
		//表单提交事件
		formSubmit(e) {
			var data = e.detail.value;
			data.type = this.type;
			if (data.account == "") {
				wx.showToast({
					title: '请输入账号',
					icon: "none",
				});
				return;
			}
			if (data.password == "") {
				wx.showToast({
					title: '请输入密码',
					icon: "none",
				});
				return;
			}
			this.request({
				url: interfaces.Login,
				data: data,
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.showToast({
							title: '登陆成功',
							icon: 'success'
						})
						app.globalData.user_info = res.data;
						setTimeout(function() {
							wx.switchTab({
								url: '../index/home',
							})
						}, 1500);
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
	}
}
